<template>
  <div id="login">
    <div class="gradient-header"></div>
    <div class="panel">
      <h2>로그인</h2>
      <form @submit.prevent="submit">
        <div class="input-area">
          <input v-model="id" placeholder="ID" required>
        </div>
        <div class="input-area">
          <input v-model="password" type="password" autocomplete="password" placeholder="Password" required>
        </div>
        <div class="login-message">{{ msg }}</div>
        <div class="input-area">
          <button>로그인</button>
        </div>
      </form>
      <router-link to="/join">회원가입</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      id: '',
      password: '',
      msg: ''
    }
  },
  methods: {
    /**
     * @description 서버에 로그인 요청
     */
    submit () {
      this.$http.post('/login', { id: this.id, password: this.password }).then(r => {
        if (r.data.token) {
          this.$store.commit('SET_TOKEN', r.data.token)
          this.$store.commit('SET_NAME', r.data.name)
          this.$router.push({ name: 'home' })
        }
      }).catch(e => {
        console.log(e)
        this.msg = '다시 입력해주세요'
      })
    }
  }
}
</script>

<style lang="scss">
#login {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.login-message {
  height: 30px;
}
</style>
